@font-face {
  font-family: vazir;
  src: url('./font/Vazir-Light.ttf');
}
@font-face {
  font-family: vazir-bold;
  src: url('./font/Vazir-Bold.ttf');
}

body {
  background-color: #EEEDEB;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  margin: 0;
  text-align: center;
  font-family: vazir;
}

div {
  width: 100%;
}

h3 {
  color: #3C3633;
  font-family: vazir-bold;
}

p {
  color: #747264;
}
.error {
  margin: 0;
  color: red;
  padding: 0;
}

input {
  padding: 8px;
  width: 70%;
  max-width: 400px;
  border: 1px solid #747264;
  border-radius: 0.8rem;
  text-align: right;
  font-family: vazir;
}

button {
  background-color: #747264;
  color: #EEEDEB;
  padding: 10px 15px;
  border: none;
  border-radius: 0.8rem;
  cursor: pointer;
  margin-top: 0.5em;
  margin-bottom: 1.5em;
  font-family: vazir;
}

button:disabled {
  background-color: rgb(200, 200, 200);
}

.package-button {
  width: 100%;
  height: 150px;
}
h4 {
  position: fixed;
  top: 5px;
  right: 5px;
  margin: 0;
}

.pdf-container {
  width: 100vw;
  height: 100vh;
  background-color: red;
}